@use '../../styles/mixins.scss';

@import './../../styles/variables.scss';

.container {
	display: flex;
	gap: 24px;
}

.wrapper {
	flex-grow: 1;
}

.dataWrapper {
	display: flex;
	justify-content: space-between;
	padding-bottom: 24px;
	padding-top: 26px;
	gap: 24px;
}

.dataWrapper > * {
	width: calc(50% - 12px);
}

.searchInputWrapper {
	padding-bottom: 28px;
}

.filterTagsWrapper {
	display: flex;
	justify-content: space-between;
	padding-bottom: 32px;
}

// 

.dropbtn {
	color: black;
	width: 100px;
	height: 43px;
	border: none;
	display: flex;
	align-items: flex-start;
	padding: 8px 8px 8px 16px;
	gap: 8px;
	border-radius: 99px;
	background: #f2f3f5;
	font-feature-settings: 'salt' on;
	font-family: 'Inter', Arial, Helvetica, sans-serif;
	font-size: 14px;
	font-weight: 500;
	line-height: normal;
	letter-spacing: 0.14px;
	opacity: 1;
	cursor: pointer;
	align-items: center;
}

/*  <div> -      */
.dropdown {
	position: relative;
	display: inline-block;
	opacity: 1;
}

/*   (  ) */
.dropdownContent {
	position: absolute;
	padding: 16px;
	z-index: 1;
	box-shadow:
		0px 20px 24px 0px rgba(0, 0, 0, 0.08),
		0px 12px 16px 0px rgba(0, 0, 0, 0.04),
		0px 4px 8px 0px rgba(0, 0, 0, 0.04),
		0px 0px 1px 0px rgba(0, 0, 0, 0.04);
	border: 1px solid #e7e7ea;
	border-radius: 8px;
	background-color: #fff;
}

/*     */
.dropdownContent p {
	@include mixins.text;
	color: black;
	padding: 5px;
	text-decoration: none;
	display: block;
	cursor: pointer;
	width: 270px;
}

/*       */
.dropdownContent p:hover {
	background-color: #f1f1f1;
}

/*      */
// .dropdown:hover .dropdownContent {
// 	display: block;
// }

/*          */
.dropdown:hover .dropbtn {
	opacity: 0.6;
	transform: 1s opacity ease-in-out;
}
